/**
 * @param {string} path
 * @returns {Boolean}
 */
export function isExternal(path) {
  return /^(https?:|mailto:|tel:)/.test(path)
}

export function isUserName(str) {
  let _result;
  if (str.length < 6) {
    _result = false;
  } else {
    var reg = /^[A-Za-z]([A-Za-z].*[0-9]|[0-9].*[A-Za-z]|[0-9])/;
    _result = reg.test(str);
  }
  return _result;
}
//验证密码 6-16位字母数字组合
export function isPsw(str) {
  let _result;
  if (str.length < 6 || str.length > 18) {
    _result = false;
  } else {
    var reg = /[A-Za-z].*[0-9]|[0-9].*[A-Za-z]|[0-9]/;
    _result = reg.test(str);
  }
  return _result;
}

export function isMobile(str) {
  const reg = /^1[3|4|5|6|7|8|9][0-9]\d{8}$/;
  if (reg.test(str)) {
    return true;
  }
  return false;
}

export function isValidIP(ip) {
  let reg = /^(\d{1,2}|1\d\d|2[0-4]\d|25[0-5])\.(\d{1,2}|1\d\d|2[0-4]\d|25[0-5])\.(\d{1,2}|1\d\d|2[0-4]\d|25[0-5])\.(\d{1,2}|1\d\d|2[0-4]\d|25[0-5])$/;
  return reg.test(ip);
}

export function isMoney(data) {
  return /^\d+(\.\d{1,2})?$/.test(data) || /^\d$/.test(data);
}

/**
 * @param {string} url
 * @returns {Boolean}
 */
export function validURL(url) {
  const reg = /^(http|https|ftp)?:\/\/([a-zA-Z0-9.-]+(:[a-zA-Z0-9.&%$-]+)*@)*((25[0-5]|2[0-4][0-9]|1[0-9]{2}|[1-9][0-9]?)(\.(25[0-5]|2[0-4][0-9]|1[0-9]{2}|[1-9]?[0-9])){3}|([a-zA-Z0-9-]+\.)*[a-zA-Z0-9-]+\.(com|edu|gov|int|mil|net|org|biz|arpa|info|name|pro|aero|coop|museum|[a-zA-Z]{2}))(:[0-9]+)*(\/($|[a-zA-Z0-9.,?'\\+&%$#=~_-]+))*$/;
  return reg.test(url);
}
